<template>
  <div>
    <div class="bgc">
      <div class="login_box">
        <div class="login">
          <div class="hellow pdt50 pdl40">你好，</div>
          <div class="tip_title pdl40">注册魅资莱，共享M肤</div>
          <div class="pdl40 flex-align-items xingming">
            <img class="img_xmicon" src="../../assets/photo/xmicon.png">
            <input v-model.trim="name" placeholder="请输入您的姓名">
          </div>

          <div class="flex-align-items pdl40">
            <img class="img_shouji" src="../../assets/photo/shouji.png">
            <input v-model.trim="newPhone" placeholder="请输入您的手机号码" type="number" maxlength="11">
          </div>

          <div class="flex-align-items pdl40">
            <img class="img_yanzheng" src="../../assets/photo/yanzhengma1.png">
            <input v-model.trim="code" placeholder="请输入手机验证码" type="number">

            <div
              class="fc-fen"
              style="width:145px;"
              :class="{disabled: !this.canClick}"
              @click="countDown"
            >{{content}}</div>
          </div>
          <div class="pw_login fs12 pdl40">注册代表你同意《魅资莱隐私协议》</div>
          <div class="pdl40">
            <button class="act" @click="toNext">注册</button>
          </div>
        </div>
      </div>
    </div>
    <div class="pd-15">
      <span>已有账号，</span>
      <span class="fc-fen" @click="toLogin">立即登录</span>
    </div>
  </div>
</template>

<script>
import { Notify } from "vant";
import { Toast } from "vant";
export default {
  data() {
    return {
      newPhone: "",
      name: "",
      code: "",

      content: "获取验证码",
      totalTime: 60, //倒计时
      canClick: true
    };
  },
  created() {},
  methods: {
    //倒计时
    countDown() {
      if (!this.canClick) return;
      this.canClick = false;
      Toast.loading({ mask: true, message: "加载中..." });
      // let postData = this.$qs.stringify({
      //   mobile: this.newPhone,
      //   type: 1
      // });

      let param = new URLSearchParams();
      param.append("mobile", this.newPhone);
      param.append("type", 1);
      this.axios.post(this.API + "/api/v1/send/code", param).then(res => {
        let resdata = res.data;
        console.log(res.data, "codecode");
        if (res.code == 200) {
          let code = 123456;
          this.code = code;
        } else {
          Toast.clear();
          Toast(resdata.msg);
        }
      });

      this.content = this.totalTime + "(s)";
      let clock = window.setInterval(() => {
        this.totalTime--;
        this.content = this.totalTime + "(s)";
        if (this.totalTime < 0) {
          window.clearInterval(clock);
          this.content = "重新获取";
          this.totalTime = 60;
          this.canClick = true;
        }
      }, 1000);
    },
    // getCode() {
    //   Toast.loading({ mask: true, message: "加载中..." });

    //   let param = new URLSearchParams();
    //   param.append("mobile", this.newPhone);
    //   this.axios.post(this.API + "/api/v1/send/code", param).then(res => {
    //     let resdata = res.data;
    //     console.log(res.data, "codecode");
    //     if (res.code == 200) {
    //     } else {
    //       Toast.clear();
    //       Toast(resdata.msg);
    //     }
    //   });
    // },
    //注册
    toNext() {
      console.log(this.newPhone);
      if (this.name == "") {
        Toast("请填写姓名");
        return;
      }
      if (this.newPhone == "") {
        Toast("请填写手机号码");
        return;
      }
      if (this.code == "") {
        Toast("请填写验证码");
        return;
      }

      if (this.newPhone.length != 11) {
        Toast("手机号长度有误");
        return;
      }
      // var telphone = /^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/;
      // if (!telphone.test(this.newPhone)) {
      //   Toast("手机号码有误，请重填");
      //   return;
      // }
      // this.$router.push({ path: "/InputCode/" + this.newPhone });

      Toast.loading({ mask: true, message: "加载中..." });
      // let postData = this.$qs.stringify({
      //   username: this.name,
      //   mobile: this.newPhone,
      //   code: this.code
      // });

      let param = new URLSearchParams();
      param.append("username", this.name);
      param.append("mobile", this.newPhone);
      param.append("code", this.code);

      this.axios
        .post(this.API + "/api/v1/user/binding", param)
        .then(res => {
          console.log(res.data, "binding");
          if (res.data.code == 200) {
            Toast.clear();
            Toast(res.data.msg);
            this.$router.push({ path: "/login" });
          } else {
            Toast.clear();
            Toast(res.data.msg);
          }
        })
        .catch(error => {
          Toast.clear();
          Toast("网络出错");
        });
    },
    //登录
    toLogin() {
      this.$router.push({ path: "/login" });
    }
  }
};
</script>

<style scoped>
.pdt50 {
  padding-top: 50px;
}
.pdt40 {
  padding-top: 40px;
}
.pdl40 {
  padding-left: 40px;
}
.pd-tb10 {
  padding: 10px 0;
}
.fs12 {
  font-size: 12px;
}
.login_box {
  width: 100%;
  height: 100%;
  padding-bottom: 30px;
}
.login {
  width: 270px;
  height: 100%;
}
.hellow {
  font-size: 20px;
  font-weight: 700px;
}
.tip_title {
  font-size: 18px;
  font-weight: 600px;
  padding-top: 30px;
}
.xingming {
  margin-top: 20px;
  width: 230px;
  height: 50px;
  background-color: rgba(255, 255, 255, 1);
  box-shadow: 2px 1px 7px 1px rgba(251, 238, 238, 0.5);
}
.img_xmicon,
.img_yanzheng {
  width: 15px;
  height: 16px;
  margin-right: 10px;
}
.img_shouji {
  width: 13px;
  height: 19px;
  margin-right: 10px;
}
input {
  width: 100%;
  line-height: 40px;
}
.pw_login {
  color: #aeaeae;
  padding: 30px 0 30px 40px;
}

.act {
  width: 80px;
  height: 45px;
  background-image: linear-gradient(90deg, #ff5491, #fe4171 100%);
  color: #fff;
  margin-top: 20px;
}

.disabled {
  color: grey;
  cursor: not-allowed;
}
</style>



